import Link from 'next/link'

const Layout = props => (

    <div>
        <nav className="navbar navbar-expand-lg navbar-light bg-light shadow fixed-top">
            <div className="container">
                <a className="navbar-brand" href="#"><i className="fas fa-comments fa-2x iconhead"></i>Tomyum Playground</a>
                <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarResponsive"
                    aria-controls="navbarResponsive" aria-expanded="false" aria-label="Toggle navigation">
                    <span className="navbar-toggler-icon"></span>
                </button>
                <div className="collapse navbar-collapse" id="navbarResponsive">
                    <ul className="navbar-nav ml-auto">
                        <li className="nav-item active">
                            <Link href="/"><a className="nav-link">Home
                                <span className="sr-only">(current)</span>
                            </a></Link>
                        </li>
                        <li className="nav-item">
                            <Link href="/about"><a className="nav-link">About</a></Link>
                        </li>
                        <li className="nav-item">
                            <Link href="/gallery"><a className="nav-link">Gallerry</a></Link>
                        </li>
                        <li className="nav-item">
                            <Link href="/contact"><a className="nav-link">Contact</a></Link>
                        </li>
                    </ul>
                </div>
            </div>
        </nav>
        <div class="content">
            {props.children}
        </div>

        <footer className="py-5 bg-light">
            <div className="container">
                <p className="m-0 text-center text-dark">Copyright &copy; Your Website 2019</p>
            </div>
        </footer>
    </div>



)

export default Layout